.NavItem {
    margin: 1rem 0;

    display: block;
    width: 100%;
}

.NavItem a {
    color: var(--color-white);
    text-decoration: none;

    font-size: 1.5rem;
    font-weight: 300;
    letter-spacing: 1px;
    border: 2px solid var(--color-white);

    width: 100%;
    margin-right: 1rem;
    padding: 1rem 0.5rem;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

.NavItem a:hover,
.NavItem a:active,
.NavItem a.active {
    color: var(--color-theme-dark);
    background-color: var(--color-white);
    border: 2px solid var(--color-blue);
}


@media (min-width: 500px) {
    .NavItem {
        margin: 0;
        display: -ms-flexbox;
        display: flex;
        height: 100%;
        width: auto;
        -ms-flex-align: center;
            align-items: center;
    }
    
    .NavItem a {
        color: var(--color-white);
        height: 100%;
        padding: 1.5rem 1rem;
    }
    
    .NavItem a:hover,
    .NavItem a:active,
    .NavItem a.active {
        background-color: var(--color-white);
        border: 2px solid var(--color-blue);
        color: var(--color-theme-dark);
    }
}


