*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

:root{
  --color-white: #fff;
  --color-black: #000;
  
  --color-blue: #40A4C8;

  --color-grey: #ccc;
  
  --color-theme-light:  #CF8F2E;
  --color-theme-dark: #703B09;
  --color-order-btn: #966909;
}

html{
  font-size:62.5%;
}

@media(max-width: 900px){
  html{
    font-size: 55%;
  }
}


@media(max-width: 700px){
  html{
    font-size: 50%;
  }
}


@media(max-width: 500px){
  html{
    font-size: 40%;
  }
}

body {
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: justify;
      justify-content: space-between;

  font-family: "Open Sans" , sans-serif;
  background-image: -webkit-gradient(linear, left top, right top, from(var(--color-white)), to(var(--color-grey)));
  background-image: -webkit-linear-gradient(left, var(--color-white), var(--color-grey));
  background-image: -o-linear-gradient(left, var(--color-white), var(--color-grey));
  background-image: linear-gradient(to right, var(--color-white), var(--color-grey));
}



